:root{--primary-color: #3f4257;--secondary-color: #ff5e3a;--primary-text-color: #9a9fbf;--active-text-color: #515365;--danger: #f92552;--success: #08ddc1}body{background-color:#edf2f6}*{margin:0;padding:0;outline:none;font-family:Roboto, -apple-system, system-ui, BlinkMacSystemFont, "Segoe UI", "Helvetica Neue", Arial, sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;box-sizing:border-box}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}a{text-decoration:none;color:var(--primary-text-color)}ul{list-style:none}.sidebar__friend{display:flex;align-items:center;padding:13px 25px}.sidebar__friend:hover{cursor:pointer;background-color:#f3f3f3}.sidebar__friend.active .sidebar__friend__avatar_wrapper span{background-color:var(--success) !important}.sidebar__friend i{color:#888da8;margin-left:auto}.sidebar__friend__avatar{width:36px;height:36px;border-radius:50%;background-color:#eee;margin-right:8px}.sidebar__friend__avatar_wrapper{position:relative}.sidebar__friend__avatar_wrapper>span{position:absolute;top:1px;left:2px;width:6px;height:6px;border-radius:50%;background-color:var(--danger);border:1px solid #fff;z-index:1}.sidebar__friend__descr{display:flex;flex-direction:column;align-items:flex-start}.sidebar__friend__descr_title{color:var(--active-text-color);font-size:12px;font-weight:700;line-height:1.3}.sidebar__friend__descr_title:hover{color:var(--secondary-color)}.sidebar__friend__descr_status{text-transform:uppercase;font-size:8px;font-weight:700;line-height:1.5;color:#888da8}
