:root{--primary-color: #3f4257;--secondary-color: #ff5e3a;--primary-text-color: #9a9fbf;--active-text-color: #515365;--danger: #f92552;--success: #08ddc1}body{background-color:#edf2f6}*{margin:0;padding:0;outline:none;font-family:Roboto, -apple-system, system-ui, BlinkMacSystemFont, "Segoe UI", "Helvetica Neue", Arial, sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;box-sizing:border-box}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}a{text-decoration:none;color:var(--primary-text-color)}ul{list-style:none}.addPost{display:flex;flex-direction:column;padding:25px;border-radius:5px;background-color:#fff;margin-bottom:20px}.addPost__form{display:flex;align-items:flex-start;justify-content:flex-start}.addPost__avatar{width:36px;height:36px;border-radius:50%;background-color:#eee;margin-right:10px}.addPost__avatar+div{display:flex;flex-direction:column;align-items:flex-end;flex-grow:1}.addPost__textarea{width:100%;height:200px;padding:5px 15px;resize:none;position:relative;border:none;margin-bottom:20px}.addPost__textarea:focus{border:1px solid var(--secondary-color)}.addPost__label{position:absolute;top:18px;left:10px}.addPost__btn{display:inline-block;font-weight:700;text-align:center;vertical-align:middle;user-select:none;background-color:transparent;border:1px solid transparent;padding:.8rem 2.1rem;font-size:.688rem;line-height:1.5;border-radius:0.3rem;transition:all .15s ease-in-out;cursor:pointer;color:#fff;background-color:var(--secondary-color);color:#fff;background-color:var(--secondary-color)}.addPost__btn:hover{opacity:0.9}
