:root{--primary-color: #3f4257;--secondary-color: #ff5e3a;--primary-text-color: #9a9fbf;--active-text-color: #515365;--danger: #f92552;--success: #08ddc1}body{background-color:#edf2f6}*{margin:0;padding:0;outline:none;font-family:Roboto, -apple-system, system-ui, BlinkMacSystemFont, "Segoe UI", "Helvetica Neue", Arial, sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;box-sizing:border-box}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}a{text-decoration:none;color:var(--primary-text-color)}ul{list-style:none}.post{display:flex;flex-direction:column;padding:25px;border-bottom:1px solid #e6ecf5;background:#fff;margin-bottom:10px}.post__info{display:flex;justify-content:flex-start;margin-bottom:20px}.post__info__avatar{width:36px;height:36px;border-radius:50%;background-color:#eee;margin-right:10px}.post__info__title{font-weight:700;color:var(--active-text-color);font-size:.725rem}.post__info__time{font-size:.725rem;font-weight:400;line-height:1.5;color:var(--primary-text-color)}.post__content__text{font-size:.875rem;font-weight:400;line-height:1.5;color:var(--primary-text-color);padding-bottom:20px;border-bottom:1px solid #eee}.post__likes{display:flex;align-items:center;padding-top:10px}.post__likes i{color:var(--primary-text-color);margin-right:8px}.post__likes i:hover{cursor:pointer;color:var(--secondary-color)}.post__likes__text{font-weight:400;line-height:1.5;color:var(--primary-text-color);font-size:12px}
